@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);

.color-white{
    color: white !important;
}

body{
    overflow: hidden;
}

.navMain{
    border: 1px solid black;
    padding: 0px;
    margin: 0px; 
}

/* Navgation Logo Centered or not */
@media only screen and (max-width: 991px) {
    .homeLogo{
        position: relative;
        left: 45%;
    }
}


/* Small devices (landscape phones, 375px and up) */
@media only screen and (min-width: 375px) {
    .navCollapse{
        text-align: center;
        font-family: "Roboto";
        background-color: #FCE7A4;
        font-size: 40px;
        min-height: 375px;
    }
}

/* Small devices (landscape phones, 576px and up) */
@media only screen and (min-width: 576px) {
    .navCollapse{
        text-align: center;
        font-family: "Roboto";
        background-color: #FCE7A4;
        font-size: 40px;
        min-height: 578px;
    }
}

/* Medium devices (tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    .navCollapse{
        text-align: center;
        font-family: "Roboto";
        background-color: #FCE7A4;
        font-size: 40px;
        min-height: 400vh;
        min-width: 768px;
    }
 }

/* Large devices (desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .navCollapse{
        text-align: center;
        font-family: "Roboto";
        background-color: #FCE7A4;
        font-size: 40px;
        min-height: 992px;
    }

 }

 @media only screen and (min-width: 1024px) {
    .navCollapse{
        text-align: center;
        font-family: "Roboto";
        background-color: #FCE7A4;
        font-size: 40px;
        min-height: 1024px;
        min-width: 1005;
    }
    
 }



/* Extra large devices (large desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    .navCollapse{
        display: none !important;
        text-align: center;
        font-family: "Roboto";
        background-color: #EFA80A;
        font-size: 25px;
        min-height: 0px;
        float: right;
    }
    Navbar{
        min-width: 1200px;
    }
    a{
        position: relative;
    }

 }




#navbar{
        display: flex;
        height: 100%;
        padding: 0px;
        margin: 0px; 
    }

#container{
    padding: 0px;
    margin: 0px; 
}
.video{
    margin: 20px 40px;
    height: 246px;
    background-color: red;
}


@media (min-width: 300px) {
    .homePage{
        margin-top: -50% !important;
        padding-top: 0px !important;
        text-align: center;
        min-width: 280px;
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
    .splash{
        position: absolute;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
    .homePage{
        margin-top: 0% !important;
        text-align: center;
        min-width: 100%;
    }
    .splash{
        position: absolute;
    }
 }

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
    .splash{
        min-width: 25%;
        min-height: 600px;
        margin-left: 30%;
        position: absolute;
        padding: 0;
        text-align: center;
    }
 }

 /* large devices  like iPad pro(large desktops, 1024px and up) */
@media (min-width: 1024px) {
    .homePage{
        margin-top: 0px;
        text-align: center;
        min-width: 1005px;
    }
    .splash{
        min-width: 25%;
        min-height: 600px;
        margin-left: 30%;
        position: absolute;
        padding: 0;
        text-align: center;
    }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
    .splash{
        min-width: 25%;
        min-height: 600px;
        margin-left: 30%;
        position: absolute;
        padding: 0;
        text-align: center;
    }
    .homePage{
        margin-top: 0px;
        text-align: center;
        margin-left: 5%;
        min-width: 100%;
        min-height: 100%;
    }
 }
.title{
    padding-top: 21px;
    color:#000000;
    font-family:Acme;
    font-size: 29px;
}

.calendar-countdown{
    border: black solid 3px;
    border-radius: 14px;
}

.calendar-top{
    padding-top: 10px;
    background-color: #EEA809;
    border-bottom: 1px solid black;
    border-radius: 10px 10px 0 0;
}


.calendar-title{
    color: #000000;
    font-family:RoundedMplus1c-Black;
    font-size:20px;
    text-align: center;
}

.calendar-items{
    border-radius:  0 0 10px 10px;
}

.calendar-item{
    text-align: center;
}

.calendar-item:first-child{
    border-right: 4px solid #EEA809;
}

.calendar-item:last-child{
    border-left: 4px solid #EEA809;
}

.duck{
    background-color: green;
}

.countdown{
    font-family:RoundedMplus1c-Black;
    font-size:37px;
}

.time{
    font-family:RoundedMplus1c-Black;
    font-size: 20px;
}
